<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/platecategory' }">板块分类</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改分类' : '新增分类' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="90px" class="demo-ruleForm">
        <el-form-item label="分类名称" prop="name">
          <el-input size="small" style="width: 300px" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="分类图片" prop="goodPic">
          <el-upload ref="uploadMainPicture" class="picMain" multiple :limit="1" :file-list="ruleForm.fileListMain" list-type="picture-card" :action="uploadActionMain()" :on-remove="handleRemoveMain" :on-success="uploadSuccessMain" :before-upload="beforeUpload">
            <i class="el-icon-plus"></i>
          </el-upload>
          <div class="font_size">
            <!-- <span>支持jpg／png格式，建议尺寸： 450 * 80</span> -->
          </div>
        </el-form-item>
        <el-form-item label="分类描述" prop="description">
          <el-input size="small" style="width: 300px" v-model="ruleForm.describes" type="textarea" placeholder="请输入描述" maxlength="30" show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        name: '',
        fileListMain: [],
        describes: ''
      },
      rules: {
        name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }],
        describes: [{ required: true, message: '请输入分类描述', trigger: 'blur' }],
        fileListMain: [{ required: true, message: '请上传分类图片', trigger: 'blur' }]
      },
      uploadImgMain: false
    }
  },

  methods: {
    // 监听关闭添加图片对话框
    handleImgClose(down) {
      this.$refs.upload.clearFiles()
      down()
    },

    handleRemoveMain(file, fileList) {
      console.log(fileList.length)
      console.log(this.ruleForm.fileListMain.length)
      if (fileList.length != this.ruleForm.fileListMain.length) {
        let fileName = file.response.data.fileName
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.fileListMain.length; i < len; i++) {
          if (this.ruleForm.fileListMain[i].response.data.fileName === fileName) {
            index = i
            break
          }
          console.log(index)
        }
        this.ruleForm.fileListMain.splice(index, 1)
      }

      if (fileList.length < 1) {
        document.querySelector('.picMain .el-upload--picture-card').style.display = 'block'
      }
    },
    // 上传图片成功
    uploadSuccessMain(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.fileListMain = fileList
      console.log(this.ruleForm.fileListMain)
      if (this.ruleForm.fileListMain.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
    },
    // 上传图片之前
    beforeUpload() {
      if (this.ruleForm.fileListMain.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
    },
    beforeAvatarUpload(file) {
      this.isUpload = true
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG / png 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    uploadActionMain() {
      return this.$picUpload
      // return 'http://localhost:8888/api/img/'
    },
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          this.ruleForm.fileListMain.length === 0 ? (this.uploadImgMain = true) : (this.uploadImgMain = false)
          if (params['fileListMain'] && params['fileListMain'].length > 0)
            params['picture'] = JSON.stringify(
              params['fileListMain']
                .map(o => o.response.data.fileName)
                .join(',')
                .split(',')
            ).toString()
          else delete params['picture']
          if (this.type == 'add') {
            this.httpPost('/column/category/add', params)
              .then(res => {
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/plateCategory',
                  query: {}
                })
              })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          } else {
            this.httpPost('/column/category/mod', params)
              .then(res => {
                this.$message.success('设置成功'),
                  this.$router.push({
                    path: '/plateCategory',
                    query: {}
                  })
              })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.type = 'edit'
      var res = params.info
      this.ruleForm = params.info
      this.ruleForm.fileListMain = []
      if (res['picture']) {
        console.log(JSON.parse(res['picture']))
        JSON.parse(res['picture']).forEach(fn =>
          this.ruleForm.fileListMain.push({
            response: {
              data: {
                fileName: fn
              }
            },
            url: this.$picDownload + fn
          })
        )
        if (this.ruleForm.fileListMain.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
      }
    } else {
      this.type = 'add'
    }
  }
}
</script>

<style lang="scss" scoped>
.hidden {
  visibility: hidden;
}
.row_item {
  display: flex;
  flex-wrap: wrap;
  /deep/.el-form-item__content {
    width: 100px;
  }
}
.move_btn {
  margin-left: 50px;
  img {
    width: 20px;
    height: 20px;
    padding: 4px;
    border: 1px dashed #333;
    border-radius: 5px;
    margin: 0 5px;
    cursor: pointer;
  }
}
.stock_cont {
  /deep/.el-form-item__content {
    padding-top: 2px;
  }
}
</style>
