<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>板块分类</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="120px" class="form_border">
        <el-form-item label="分类名称" prop="name">
          <el-input v-model="queryForm.name" placeholder="请输入分类名称"></el-input>
        </el-form-item>

        <el-form-item class="select_btn">
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增分类</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column label="缩略图">
            <template slot-scope="scope">
              <img width="80" :src="imgPath(scope.row.picture)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="name" label="分类名称">
            <template slot-scope="scope">
              <span ref="name">{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="describes" label="分类描述"> </el-table-column>
          <el-table-column label="创建时间" prop="createDatetime"></el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-link type="primary" :underline="false" slot="reference" @click.stop="chooseDevice(scope.row)">选择机器</el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>
    </el-card>
    <ChooseMachine ref="ChooseMachine" @getName="getName" />
  </div>
</template>

<script>
  import ChooseMachine from '@/components/choose-machine/ChooseMachine'
  export default {
    components: { ChooseMachine },
  data() {
    return {
      queryForm: {
        name: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      dialogVisible: false,
      selectId: '', //  选机器按钮保存当前板块id
      formLabelWidth: '100px'
    }
  },
  methods: {
    // 选择后返回已选
    getName(e) {
      this.selectDeviceName = e
      console.log(this.selectId, this.selectDeviceName)
      let params = {
        columnId: this.selectId,
        deviceIds: this.selectDeviceName
      }
      //TODO 后台调用板块绑定机器接口
      // this.httpPost('/columns/devices', params).then(ret => {
      //   this.$message({ type: 'success', message: '绑定成功!' })
      //   this.loadData()
      // })
    },
    chooseDevice(item){
      this.selectId = item.id
      this.$refs.ChooseMachine.selected(item.deviceId)
    },
    imgPath(mainPic) {
      if (!mainPic) return ''
      return this.$picDownload + mainPic.substring(2, mainPic.length - 2)
    },
    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/column/category/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },
    // 搜索
    search() {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editPlateCategory', name: 'EditPlateCategory', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editPlateCategory', name: 'EditPlateCategory', params: { type: 'edit', info } })
    },

    editdischarging() {
      this.dialogVisible = true
    },

    loadData() {
      this.httpGet('/column/category/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
</style>
