<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>板块详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="120px" class="form_border">
        <el-form-item label="板块分类" prop="columnCategoryId">
          <el-select clearable v-model="queryForm.columnCategoryId" placeholder="请选择板块分类">
            <el-option v-for="option in options" :key="option.id" :label="option.name" :value="option.id"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="版块标题" prop="title">
          <el-input v-model="queryForm.title" placeholder="请输入版块标题"></el-input>
        </el-form-item>

        <el-form-item class="select_btn">
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增板块</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="categoryName" label="板块分类"> </el-table-column>
          <el-table-column prop="title" label="版块标题"> </el-table-column>
          <el-table-column prop="createDatetime" label="创建时间"> </el-table-column>
          <el-table-column label="已选机器">
            <template slot-scope="scope">
              <div style="cursor: pointer; color: #40A9FE" @click.stop="checkMachine(scope.row.deviceMac)">查看已选机器</div>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="160">
            <template slot-scope="scope">
              <el-link type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row)">选择机器</el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>

      <!-- 查看已选机器 -->
      <el-dialog title="选择机器" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        {{ deviceName }}
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </div>
      </el-dialog>
      <ChooseMachine ref="ChooseMachine" @getName="getName" />
    </el-card>
  </div>
</template>

<script>
import ChooseMachine from '../../components/choose-machine/ChooseMachine'
export default {
  components: { ChooseMachine },
  data() {
    return {
      queryForm: {
        title: '',
        columnCategoryId: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      options: [],
      dialogVisible: false,
      deviceName: '',
      selectId: '', //  选机器按钮保存当前id
      formLabelWidth: '100px'
    }
  },
  methods: {
    // 选择后返回已选
    getName(e) {
      this.selectDeviceName = e
      console.log(this.selectId, this.selectDeviceName)
      let params = {
        columnId: this.selectId.toString(),
        deviceIds: this.selectDeviceName.toString()
      }
      this.httpPost('/columns/devices', params).then(ret => {
        this.$message({ type: 'success', message: '绑定成功!' })
        this.loadData()
      })
    },
    // 选择机器
    putaway(item) {
      this.selectId = item.id
      this.$refs.ChooseMachine.selected(item.deviceId)
    },
    checkMachine(deviceName) {
      this.deviceName = deviceName
      this.dialogVisible = true
    },
    // 搜索
    search() {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editclassify', name: 'EditClassify', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editclassify', name: 'EditClassify', params: { type: 'edit', info } })
    },

    onSubmit() {
      console.log(this.queryForm)
    },

    choose(id) {
      this.dialogVisible = true
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/columns/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.httpGet('/columns/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
    var queryForm = {
      size: 100,
      page: 1,
      total: ''
    }
    this.httpGet('/column/category/query/', queryForm).then(res => {
      this.options = res.rows
    })
  }
}
</script>

<style lang="scss" scoped></style>
