<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/classify' }">板块详情</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改板块详情' : '新增板块详情' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="板块标题" prop="title">
          <el-input size="small" style="width: 300px" v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="标题图片" prop="fileList">
          <el-upload class="pics" ref="uploadPictures" :file-list="ruleForm.fileList" multiple :limit="5" list-type="picture-card" :action="uploadAction()" :on-remove="handleRemove" :on-success="uploadSuccess" :before-upload="beforeUpload">
            <i class="el-icon-plus"></i>
          </el-upload>
          <div>支持 png/jpg 格式，图片最小尺寸 480*480，最大 5M;最多上传5张</div>
        </el-form-item>
        <el-form-item label="板块分类" prop="columnCategoryId">
          <el-select v-model="ruleForm.columnCategoryId" placeholder="请选择板块分类">
            <el-option v-for="option in options" :key="option.id" :label="option.name" :value="option.id"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '../../components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  data() {
    return {
      ruleForm: {
        title: '',
        fileList: [],
        columnCategoryId: '',
        content: ''
      },
      rules: {
        title: [{ required: true, message: '请输入标题', trigger: 'blur' }],
        fileList: [{ required: true, message: '请上传图片', trigger: 'blur' }],
        columnCategoryId: [{ required: true, message: '请选择板块分类', trigger: 'blur' }],
        content: [{ required: true, message: '请输入内容', trigger: 'blur' }]
        // detialPic: [{ required: true, message: '请上传商品详情图', trigger: 'blur' }],
        // Cooler: [{ required: true, message: '请选择饮品冷热', trigger: 'blur' }]
      },
      options: [],
      info: false
    }
  },

  methods: {
    uploadAction() {
      return this.$picUpload
      // return ''
    },

    // 删除数组中图片
    handleRemove(file, fileList) {
      console.log(fileList.length)
      console.log(this.ruleForm.fileList.length)
      if (fileList.length != this.ruleForm.fileList.length) {
        let fileName = file.response.data.fileName
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.fileList.length; i < len; i++) {
          if (this.ruleForm.fileList[i].response.data.fileName === fileName) {
            index = i
            break
          }
        }
        this.ruleForm.fileList.splice(index, 1)
        console.log(this.ruleForm.fileList)
      }
      if (fileList.length < 5) {
        document.querySelector('.pics .el-upload--picture-card').style.display = 'block'
      }
    },

    // 上传图片成功
    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.fileList = fileList
      if (this.ruleForm.fileList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
    },

    // 上传图片之前
    beforeUpload() {
      if (this.ruleForm.fileList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
    },

    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.fileList.length === 0 ? (this.uploadImg = true) : (this.uploadImg = false)
          this.ruleForm.content === '' ? (this.info = true) : (this.info = false)
          if (!this.uploadImg && !this.info) {
          let params = Object.assign({}, this.ruleForm)
          if (params['fileList'] && params['fileList'].length > 0)
            params['pictures'] = JSON.stringify(
              params['fileList']
                .map(o => o.response.data.fileName)
                .join(',')
                .split(',')
            ).toString()
          else delete params['pictures']
          console.log(params)
          if(this.type =='add'){
            this.httpPost("/columns/add",params).then(res => {
              this.$message.success('设置成功')
              this.$router.push({
                path: '/plate',
                query: {}
              })
            })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
          else{
            this.httpPost("/columns/mod",params).then(res => {
              this.$message.success('设置成功'),
                this.$router.push({
                  path: '/plate',
                  query: {}
                })
            })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
          // this.$router.back()
          } else {
            console.log('error submit!!')
            return false
          }
        }
      })
    },

    // 富文本
    getCont(val) {
      console.log(val)
      this.ruleForm.content = val
    },

    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    let queryForm = {
      size: 10,
      page: 1,
      total: '',
      specialMaterial: 0
    }
    this.httpGet('/column/category/query/', queryForm).then(res => {
      this.options = res.rows
    })
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else if (this.$route.params.type === 'edit') {
      var res = this.$route.params.info
      this.type = 'edit'
      this.ruleForm = this.$route.params.info
      this.$refs.bzlc.editorValue = this.ruleForm.content
      this.ruleForm.fileList = []
      // this.$refs.quillEditor.getContent(this.ruleForm.content)
      if (res['pictures']) {
        console.log(JSON.parse(res['pictures']))
        JSON.parse(res['pictures']).forEach(fn =>
          this.ruleForm.fileList.push({
            response: {
              data: {
                fileName: fn
              }
            },
            url: this.$picDownload + fn
          })
        )
        if (this.ruleForm.fileList.length >= 5) document.querySelector('.pic .el-upload--picture-card').style.display = 'none'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.hidden {
  visibility: hidden;
}
.row_item {
  display: flex;
  flex-wrap: wrap;
  /deep/.el-form-item__content {
    width: 100px;
  }
}
.move_btn {
  margin-left: 50px;
  img {
    width: 20px;
    height: 20px;
    padding: 4px;
    border: 1px dashed #333;
    border-radius: 5px;
    margin: 0 5px;
    cursor: pointer;
  }
}
.stock_cont {
  /deep/.el-form-item__content {
    padding-top: 2px;
  }
}
</style>
